












































import addCard from "@/components/addCard/addCard.vue";
var userLoginInfo;
export default {
	components: {
		addCard
	},
	data() {
		return {
			exMony: '',
			remainder: '',
			card: [],
			bankId: '',
			cartI: '',
			orderId: '',
			openAttr: false,
			cardList: [],
			isdefault: true
		}
	},
	onShow() {

	},
	onLoad(params) {
		// 账户余额
		// console.log(params);
		userLoginInfo = this.userLoginInfo('../mine/mine', 2);
		this.getCard();
		if (params.remainder) {
			this.remainder = params.remainder;
			return;
		}
	},
	methods: {
		// 选择银行卡
		showBanner: function() {
			if (this.openAttr === false) {
				// 打开规格选择弹窗
				this.openAttr = !this.openAttr;
			}
		},
		//获取用户选中的银行卡
		selectMyCard: function(selectMyCard) {
			uni.request({
				url: this.apiUrl + 'user/getBank',
				method: 'GET',
				data: {
					uid: userLoginInfo.id,
					id: selectMyCard
				},
				success: res => {
					// console.log(res.data.data);
					if (res.data.code === 0) {
						this.card = res.data.data;
						// 修改选中银行卡id
						this.bankId = selectMyCard;
						// 截取银行卡后四位
						this.cartI = this.card.card_num.slice(-4);
						this.openAttr = false;
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 去判断该用户是否绑定了银行卡 若没绑定跳转到添加银行卡页面
		//否则默认提交到第一张银行卡上
		getCard: function() {
			uni.request({
				url: this.apiUrl + 'user/getBankList',
				method: 'GET',
				data: {
					uid: userLoginInfo.id
				},
				success: res => {
					if (res.data.code === 0) {
						if (Array.isArray(res.data.data) && res.data.data.length === 0) {
							this.isdefault = false;
							uni.showToast({
								title: '您还未绑定银行卡',
								icon: 'none',
								success: (res) => {
									setTimeout(() => {
										uni.redirectTo({
											url: '../addCard/addCard'
										})
									}, 300);
								}
							})
						} else {
							// console.log(res.data.data);
							this.card = res.data.data[0];
							this.bankId = this.card.id;
							// 截取银行卡后四位
							this.cartI = this.card.card_num.slice(-4);
							// 								// console.log(this.cartI);
							this.cardList = res.data.data;
						}
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 提交提现金额 和 提交的银行卡
		goCash: function() {
			if (this.remainder < this.exMony) {
				uni.showToast({
					title:'超出了您的零钱余额，请重试',
					icon:'none',
					success: (res) => {
						return;
					}
				})
				
			}
			uni.request({
				// user/saveUserCash
				url: this.apiUrl + 'user/saveUserCash',
				method: 'POST',
				header: {
					'content-type': "application/x-www-form-urlencoded"
				},
				data: {
					uid: userLoginInfo.id,
					price: this.exMony,
					bank_id: this.bankId
				},
				success: res => {
					if (res.data.code === 0) {
						// 订单id
						this.orderId = res.data.data;
						uni.showToast({
							title: "提交成功",
							icon: 'none', //图标，支持"success"、"loading" 
							mask: true,
							duration: 1000,
							success: (res) => {
								uni.redirectTo({
									url: './extract_apply?orderId=' + this.orderId
								});
							},
						})
					}
				},
				fail: () => {},
				complete: () => {}
			});

		},
		// 关闭规格弹窗
		closeAttr() {
			this.openAttr = false;
		},
	}
}
